li {
    list-style: none; 
}
.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}


.userProfile {
    margin-left: 35%;
    margin-top: 22px;
    margin-right:0px;
};
.Drag-header {
    background-color: #ffffff;
    min-height: 100vh;
    display: flex;
    font-size: calc(10px + 2vmin);
    color: whitesmoke;
}

.drag-n-drop {
    background-color: #ffffff;
    padding: .5rem;
    display: grid;
    grid-gap: .5rem;
    gap: .5rem;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(auto-fill, 300px);
}

.dnd-group {
    padding: .5rem;
    background-color:#353941;
    border-radius: 5px;
    text-align: center;
}

.dnd-item {
    background-color: white;
    color: #282c34;
    min-height: 150px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dnd-item:not(:last-of-type) {
    margin-bottom: .5rem;
}

.dnd-item * {
    margin: 0;

}

.pdf-Button {
    font-size: 12px;
    border-radius: 12px;
    font-weight: bold;
    background-color: #bfdcae;
}
